.sub-account-management {
  &__area-header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    min-height: 42px;
  }

  &__company-select {
    width: 200px;
  }

  &__table-section {
    margin-top: 20px;
    background-color: white;
    border: 0.5px groove #ddd;
    border-radius: 10px;
    box-shadow: 0 0 0 0 black;

    .ant-table {
      border: 1px solid #ddd;
    }
  }

  &--pl10 {
    padding-left: 10px;
  }

  &--pr10 {
    padding-right: 10px;
  }
}
