.flex {
  display: flex;
  align-items: center;
}
.phone-interview-container {
  display: flex;
  height: 100%;
}
.area {
  border-top: 4px solid #0784f1;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin: 5px;
  padding: 15px 10px;
  display: flex;
}
.leftArea {
  flex-direction: column;
  align-items: center;
  height: 100%;
}
.leftsideTitle {
  display: flex;
  align-items: center;
}
.leftsideTitleText {
  font-family: 微軟正黑體;
  font-size: 18px;
}
.leftsideTitleIcon {
  font-size: 18px;
  margin: 0px 5px;
}
.datePicker {
  margin: 10px 0 5px 0;
}
.resetMonth {
  font-family: 微軟正黑體;
  font-size: 12px;
  color: #808182;
  cursor: pointer;
}
.addBtn {
  width: 100%;
  background-color: #cfd1d3;
  border: 0;
  font-weight: 900;
  margin: 10px 0;
}
.divider {
  margin: 5px 0;
}
.tipText {
  font-family: 微軟正黑體;
  font-size: 14px;
  color: #808182;
  font-weight: 900;
  margin: 40px 0;
}
.height100percent {
  height: 100%;
}
.width100percent {
  width: 100%;
}
.rightArea {
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 15px 25px;
}
.rightsideHeader {
  display: flex;
  justify-content: space-between;
}
.rightsideTitle {
  display: flex;
  align-items: center;
}
.rightsideTitleIcon {
  font-size: 20px;
  color: #0784f1;
}
.rightsideTitleText {
  font-family: 微軟正黑體;
  font-size: 18px;
  color: #808182;
  padding: 0 10px;
}
.cancelBtn {
  /* color: black !important; */
  /* background-color: #CFD1D3; */
  font-weight: 900;
}
.editSection {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.editContainer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 5px 0;
}
.editLabel {
  padding-right: 10px;
}
.editWidth {
  width: 200px;
}
.radiosSection {
  flex-grow: 1;
}
.radioContainer {
  margin: 20px 0;
}
.radioTitle {
  display: flex;
  align-items: center;
}
.radioTitleIcon {
  font-size: 18px;
}
.radioTitleText {
  font-family: 微軟正黑體;
  font-size: 18px;
  padding: 0 10px;
}
.input {
  width: 200px;
}
.discription {
  margin: 0 15px 0 50px;
}
