.home {
  min-height: 100vh;

  .home__sider {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    height: 100vh;
    overflow: auto;
  }

  .home__header {
    display: flex;
    flex-shrink: 0;
    justify-content: space-between;
    padding-right: 40px;
    background-color: white;

    .home__header-link {
      font-size: 20px;
      color: black;
    }

    .home__header-space {
      float: right;
      font-size: 20px;
    }
  }

  .collapsed {
    position: relative;
    margin-left: 80px;
    transition: 0.3s;
  }

  .non-collapsed {
    position: relative;
    margin-left: 200px;
    transition: 0.3s;
  }

  .home__content {
    flex-grow: 1;
    flex-shrink: 0;
    margin: 0 16px;
    margin: auto;
    // width: 100%;

    .site-layout-background {
      height: 100%;
      min-height: 360px;
      padding: 24px;
    }
  }

  .home__footer {
    flex-shrink: 0;

    .span--right {
      float: right;
      margin-left: 10px;
    }
  }
}

@media (max-width: 768px) {
  .home__content {
    width: 100%;
  }
}

@media (min-width: 768px) {
  .home__content {
    width: 700px;
  }
}

@media (min-width: 991px) {
  .home__content {
    width: 860px;
  }
}

@media (min-width: 1200px) {
  .home__content {
    width: 1000px;
  }
}

@media (min-width: 1600px) {
  .home__content {
    width: 1400px;
  }
}
